import { FormHelper } from "mdt-client";

let readonlyTitle = false;

FormHelper.addCommand("demo.TestTable", {
    title: "Make Readonly Title",
    command: () => {
        readonlyTitle = !readonlyTitle;
    }
});

FormHelper.addRules("demo.TestTable", {
    Price: {
        visible: false
    },
    Title: {
        readonly: () => readonlyTitle
    }
});
